const assert = require("../../../src/utils/assert");
const dogTout = dogT => {
  switch (dogT) {
    // given
    case " ":
      // then
      return ["", ""];
    // given
    case " ":
      // then
      return ["", ""];
    // given
    default:
      // then
      return "who knows";
  }
};
assert(
  "dogTout('') =>['','']",
  dogTout(""),
  ["", ""]
);
assert(
  'dogTout("") => ["",""]',
  dogTout(""),
  ["", ""]
);

module.exports = dogTout;
