export const YL = ({ data, dogType }) => {
  // given
  console.log("data in yl", data);
  // when
  const input = {
    name: data.compShortName,
    fio: data.FIO,
    INN: data.INN,
    KPP: data.KPP,
    OGRN: data.OGRN,
    yurAdress: data.YurAdress,
    bank: data.bankName,
    bik: data.BIK,
    rs: data.RS,
    ks: data.KS,
    sign: data.signature
  };
  //sconst string = ": "+input.name+" : "+input.INN+" : "+input.KPP+" : "+input.OGRN+" .: "+input.yurAdress+" : "+input.bank+" : "+input.bik+" /c: "+input.rs+" /: "+input.ks+" "+input.name+" "+input.sign+"";
  // then
  return (
    <div>
      {dogType}: {input.name}
      <br />
      : {input.INN}
      <br />
      : {input.KPP}
      <br />
      : {input.OGRN}
      <br />
      .: {input.yurAdress}
      <br />
      : {input.bank}
      <br />
      : {input.bik}
      <br />
      /c: {input.rs}
      <br />
      /: {input.ks}
      <br />
      name: {input.name}
      <br />
      : {input.signature}
      <br />
    </div>
  );
};

export const IP = ({ data, dogType }) => {
  // given
  console.log("data in ip", data);

  // when
  const input = {
    name: data.ShortName,
    INN: data.INN,
    OGRNIP: data.OGRNIP,
    yurAdress: data.YurAdress,
    bank: data.bankName,
    bik: data.BIK,
    rs: data.RS,
    ks: data.KS,
    sign: data.signature
  };
  // then
  const string =
    ": " +
    input.name +
    " : " +
    input.INN +
    " : " +
    input.OGRNIP +
    " .: " +
    input.yurAdress +
    " : " +
    input.bank +
    " : " +
    input.bik +
    " /c: " +
    input.rs +
    " /: " +
    input.ks +
    " " +
    input.name +
    " " +
    input.sign +
    "";
  return (
    <div>
      {dogType}: {input.name}
      <br />
      : {input.INN}
      <br />
      : {input.OGRNIP}
      <br />
      .: {input.yurAdress}
      <br />
      : {input.bank}
      <br />
      : {input.bik}
      <br />
      /c: {input.rs}
      <br />
      /: {input.ks}
      <br />
      name: {input.name}
      <br />
      : {input.sign}
      <br />
    </div>
  );
};

export const FL = ({ data, dogType}) => {
  // given
  console.log("data in fl", data);

  // when
  const input = {
    f: data.lastName,
    i: data.firstName,
    o: data.midName,
    doc: data.docType,
    docSer: data.Serial,
    docNum: data.number,
    docWhere: data.whoGave,
    docWhen: data.whenGave,
    docWhCode: data.codeGave,
    sign: data.signature
  };
  // then
  const string =
    ": " +
    input.f +
    " " +
    input.i +
    " " +
    input.o +
    " " +
    input.doc +
    "  " +
    input.docSer +
    "  " +
    input.docNum +
    " : " +
    input.docWhere +
    "  : " +
    input.docWhen +
    "  : " +
    input.docWhCode +
    " " +
    input.sign +
    "";
  return (
    <div>
      {dogType}: {input.f} {input.i} {input.o}
      <br />
      : {input.docSer}
      <br />
      : {input.docNum}
      <br />
      : {input.docWhere}
      <br />
       : {input.docWhen}
      <br />
       : {input.docWhCode}
      <br />
      : {input.sign}
      <br />
    </div>
  );
};

